.dialogs {
    display: grid;
    grid-template: 100% / 3fr 10fr;
    grid-gap: 5px;
    background: #edeef0;
}
.dialogWrapper {
    background: #edeef0;
    &__formInner {
        z-index: 1;
        position: relative;
    }
}
.messagesWrapper {
    background: #fff;
    &__messageInner {
        height: 100%;
        padding: 15px;
        display: flex;
        flex-flow: column;
        justify-content: flex-end;
        box-sizing: border-box;
    }
    &__formInner {
        textarea {
            width: 100%;
            padding: 15px;
            resize: none;
            box-sizing: border-box;
            margin-bottom: 15px;
        }
        button {
            float: right;
        }
    }
}
.dialogItem {
    margin-bottom: 2px;
    background: #fff;
    transition: .1s all ease-in-out;
    cursor: pointer;
    a {
        color: #000;
        text-decoration: none;
        width: 100%;
        height: 100%;
        display: block;
        padding: 15px 10px;
    }
    &:hover {
        background: rgba($color: #000000, $alpha: 0.1);
    }
}
.message {
    margin: 4px 0 4px 4px;
    padding: 10px;
    transition: .1s all ease-in-out;
    &:hover {
        background: rgba($color: #000000, $alpha: 0.1);
    }
}